.sales-pipeline {
    margin-top: var(--spacing-2xl);
    width: 100%;
}

.pipeline-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: var(--spacing-lg);
}

.pipeline-stats {
    display: flex;
    gap: var(--spacing-md);
}

.stat-chip {
    padding: 6px 16px;
    background: rgba(255, 255, 255, 0.05);
    border: 1px solid var(--glass-border);
    border-radius: var(--border-radius-full);
    font-size: 0.85rem;
    color: var(--secondary);
    display: flex;
    gap: 8px;
    align-items: center;
}

.pipeline-board {
    display: flex;
    gap: var(--spacing-lg);
    overflow-x: auto;
    padding-bottom: var(--spacing-lg);
    min-height: 500px;
}

.pipeline-column {
    flex: 1;
    min-width: 320px;
    background: rgba(255, 255, 255, 0.02);
    border: 1px solid var(--glass-border);
    border-radius: var(--border-radius-lg);
    display: flex;
    flex-direction: column;
}

.column-header {
    padding: var(--spacing-md) var(--spacing-lg);
    border-bottom: 1px solid var(--glass-border);
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.column-header h4 {
    font-size: 0.95rem;
    font-weight: 600;
    color: var(--primary);
    display: flex;
    align-items: center;
    gap: 8px;
}

.count-badge {
    background: rgba(255, 255, 255, 0.1);
    padding: 2px 8px;
    border-radius: 12px;
    font-size: 0.75rem;
    color: var(--secondary);
}

.column-content {
    padding: var(--spacing-md);
    flex: 1;
    display: flex;
    flex-direction: column;
    gap: var(--spacing-md);
    overflow-y: auto;
    max-height: 800px;
}

/* Lead Card */
.lead-card {
    background: rgba(25, 25, 35, 0.6);
    border: 1px solid var(--glass-border);
    border-radius: var(--border-radius-md);
    padding: var(--spacing-md);
    cursor: pointer;
    transition: all 0.2s ease;
    position: relative;
    overflow: hidden;
}

.lead-card:hover {
    background: rgba(40, 40, 55, 0.8);
    border-color: var(--accent);
    transform: translateY(-2px);
    box-shadow: 0 4px 20px rgba(0, 0, 0, 0.2);
}

.lead-card-header {
    display: flex;
    justify-content: space-between;
    margin-bottom: 8px;
}

.lead-name {
    font-weight: 600;
    color: var(--primary);
    font-size: 0.95rem;
}

.lead-value {
    font-size: 0.85rem;
    color: var(--fintech-green);
    font-weight: 600;
}

.lead-details {
    margin-bottom: 12px;
}

.plan-interest {
    display: inline-block;
    padding: 2px 8px;
    border-radius: 4px;
    font-size: 0.75rem;
    background: rgba(255, 255, 255, 0.05);
    color: var(--secondary);
    margin-bottom: 4px;
}

.lead-footer {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-top: 8px;
    padding-top: 8px;
    border-top: 1px solid rgba(255, 255, 255, 0.05);
}

.lead-date {
    font-size: 0.75rem;
    color: var(--secondary);
}

.btn-close-mini {
    padding: 4px 10px;
    border-radius: 4px;
    background: rgba(41, 151, 255, 0.1);
    color: var(--accent);
    font-size: 0.75rem;
    font-weight: 600;
    border: 1px solid rgba(41, 151, 255, 0.2);
    transition: all 0.2s;
}

.btn-close-mini:hover {
    background: var(--accent);
    color: white;
}

/* Paid Customer Styling */
.lead-card.paid {
    border-left: 3px solid var(--fintech-green);
}

/* Modal Styles */
.modal-overlay {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: rgba(0, 0, 0, 0.7);
    backdrop-filter: blur(5px);
    z-index: 1000;
    display: flex;
    align-items: center;
    justify-content: center;
}

.modal-content {
    background: #0f0f13;
    border: 1px solid var(--glass-border);
    border-radius: var(--border-radius-xl);
    width: 500px;
    max-width: 90vw;
    padding: 0;
    position: relative;
    box-shadow: 0 20px 50px rgba(0, 0, 0, 0.5);
    overflow: hidden;
}

.modal-header {
    padding: var(--spacing-lg);
    border-bottom: 1px solid var(--glass-border);
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.modal-header h2 {
    font-size: 1.25rem;
    font-weight: 600;
    margin: 0;
}

.modal-body {
    padding: var(--spacing-lg);
}

.form-group {
    margin-bottom: var(--spacing-md);
}

.form-group label {
    display: block;
    font-size: 0.85rem;
    color: var(--secondary);
    margin-bottom: 6px;
}

.form-input,
.form-select {
    width: 100%;
    background: rgba(255, 255, 255, 0.05);
    border: 1px solid var(--glass-border);
    border-radius: 8px;
    padding: 10px 12px;
    color: white;
    font-size: 0.95rem;
    outline: none;
    transition: border-color 0.2s;
}

.form-input:focus,
.form-select:focus {
    border-color: var(--accent);
}

.plan-selection-grid {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 12px;
    margin-top: 12px;
}

.plan-option {
    background: rgba(255, 255, 255, 0.03);
    border: 1px solid var(--glass-border);
    border-radius: 8px;
    padding: 12px;
    text-align: center;
    cursor: pointer;
    transition: all 0.2s;
}

.plan-option:hover {
    background: rgba(255, 255, 255, 0.08);
}

.plan-option.selected {
    background: rgba(41, 151, 255, 0.1);
    border-color: var(--accent);
    color: white;
}

.plan-name {
    font-size: 0.85rem;
    font-weight: 600;
    display: block;
    margin-bottom: 4px;
}

.plan-cost {
    font-size: 0.75rem;
    color: var(--secondary);
}

.confetti-container {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    pointer-events: none;
    z-index: 10;
}